export default {
	data() {
		return {
			judgehava : true,
			address : [
				
			]
			// judgehava : true,
		}
	},
	onLoad() {
	  
	},
	onShow(){
		this.req();
		
	},
	
	onHide(){
		
	},
	methods: {
		back : function(){
		   uni.navigateBack({
		
		   });
		},
		del:function(i){
			let that=this;
			uni.showModal({
			        title: '确定要删除吗',
			        content: '删除地址',
			        cancelText:'确定',
			        confirmText:'取消',
			        success(res){
						console.log(res)
						if(res.cancel){
							let url = that.url1+"/dinghai/Address/deleteAddress";
							
							uni.request({
								 url : url,
										 header : {
											 "Content-type" : "application/x-www-form-urlencoded"
										 },
										 method : 'post',
										 data : {
											 userId:that.user.uid,
											 addressId:that.address[i].addressId
										 },
										 success : function(res){
										     if(res.statusCode===200){
												 that.address.splice(i,1)
												 uni.showToast({
												 	icon:"none",
													title:"删除成功"
												 })
												 if(that.address.length >=1 ){
												 	that.judgehava = false;
												 }else{
												 	that.judgehava = true;
												 }
											 }else{
												 uni.showToast({
												 	title:"您的网络开了小差",
													icon:"none"
												 })
											 }
											
										 },
										 fail : function(err){
											 console.log(err);
											 uni.showToast({
											 	title:"连接服务器失败",
												icon:"none"
											 })
										 }
							});
						}
			        }
			   });
		},
		req:function(){
			uni.showLoading({
				title:"正在加载",
				mask:true
			})
			let that=this;
			let url = that.url1+"/dinghai/Address/selectAddress";
			uni.request({
				 url : url,
						 header : {
							 "Content-type" : "application/x-www-form-urlencoded"
						 },
						 method : 'post',
						 data : {
							 userId:that.user.uid
						 },
						 success : function(res){
						     if(res.statusCode===200){
								 that.address=res.data.flag
								 console.log(that.address)
								 uni.showToast({
								 	title:"加载成功"
								 })
							 }else{
								 uni.showToast({
								 	title:"您的网络开了小差",
									icon:"none"
								 })
							 }
							 console.log(that.address)
							 console.log(that.address.length)
							 if(that.address.length >=1 ){
							 	that.judgehava = false;
							 }else{
							 	that.judgehava = true;
							 }
						 },
						 fail : function(err){
							 console.log(err);
							 uni.showToast({
							 	title:"连接服务器失败",
								icon:"none"
							 })
						 }
			});
			
		},
		   // 去增加新地址
		goadddz : function(){
			uni.navigateTo({
				url : "tjdizhi/tjdizhi"
			})
		},
		
		   // 点击操作
		goadddzsp : function(index){
			var obj = JSON.stringify(this.address[index]);
			console.log(obj)
			uni.navigateTo({
				url : "tjdizhi/tjdizhi?obj="+obj
			});
		}
	
	}
}
